import { Component, OnDestroy, OnInit } from '@angular/core';
import { MessageService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { PasswordService } from 'src/app/shared/services/auth/password.service';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.page.html',
  styleUrls: ['./perfil.page.scss'],
})
export class PerfilPage implements OnInit, OnDestroy {

  user: UserDTO;
  senha = '';

  isLoaded = false;
  private subscriptions = new Subscription();

  constructor(
    private passwordService: PasswordService,
    private toastService: MessageService,
    private funcionarioService: FuncionarioService,
    private storage: StorageService,

  ) { }

  ngOnInit() {
    this.loadUser();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadUser() {
    const { id } = this.storage.getLocalUser().user;
    const sub = this.funcionarioService.findById(id)
      .subscribe(data => {
        this.user = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  setUserImage(user: UserDTO) {
    return user.avatar || "assets/imgs/outros/default-user.jpg";
  }

  uploadFile(event) {
    const file = event.target.files[0];

    const sub = this.funcionarioService.uploadAvatar(file)
      .subscribe(response => {
        this.presentToast('success', 'Sucesso!', 'Foto alterada!', Toast.shortDuration);
        this.isLoaded = false;
        this.loadUser();
      });
    this.subscriptions.add(sub);
  }

  trocarSenha() {
    const sub = this.passwordService.change(this.senha)
      .subscribe(response => {
        this.presentToast('success', 'Sucesso!', 'Senha alterada!', Toast.shortDuration);
      });
    this.subscriptions.add(sub);
  }

  disabledBTN() {
    return this.senha.length < 8 ? true : false;
  }


  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
